.images-list{
    display: grid ;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr) ); 
    grid-gap:0px 10px ;
   grid-auto-rows: 10px;

}
.images-list img{
    width: 250px ; 
/*grid-row-end: span 2 ;*/
} 

.editImages{
    font-size: 15px;
    display: flex;
    justify-content: center;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 10px;
}

